.burger-btn {
  width: 30px;
  height: 30px;
  margin-left: 20px;
  position: relative;
  cursor: pointer;
}

.burger-btn::after {
  content: "";
  position: absolute;
  top: 0;
  width: 30px;
  background-color: #fff;
  height: 2px;
}

.burger-btn span {
  content: "";
  position: absolute;
  top: 14px;
  width: 30px;
  background-color: #fff;
  height: 2px;
}

.burger-btn::before {
  content: "";
  position: absolute;
  bottom: 0;
  width: 30px;
  background-color: #fff;
  height: 2px;
}

